.messagePage {
  background: white;
  padding: 15px 25px;
  margin-bottom: 20px;
  -webkit-box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.05);
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.05); }
  .messagePage .messagePage-user {
    overflow: hidden; }
    .messagePage .messagePage-user .messagePage-user-avatar {
      width: 40px;
      height: 40px;
      border-radius: 50%;
      background-color: blue;
      float: left;
      margin-right: 10px;
      background: url("./../images/QQ.png") no-repeat center center;
      background-size: cover; }
    .messagePage .messagePage-user .messagePage-user-info .messagePage-user-info-name {
      color: #666;
      font-size: 14px; }
    .messagePage .messagePage-user .messagePage-user-info .messagePage-user-info-time {
      height: 25px;
      line-height: 25px;
      color: #999;
      font-size: 12px; }
  .messagePage .messagePage-text {
    max-height: 228px;
    overflow: auto;
    padding: 5px 0;
    line-height: 1.6em;
    color: #666;
    font-size: 14px; }
  .messagePage .messagePage-action {
    overflow: hidden; }
    .messagePage .messagePage-action .messagePage-action-replay {
      float: right; }



