.blogItemPage {
  width: 300px;
  padding: 5px;
  background-color: #fff;
  float: left;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }
  .blogItemPage .blogItemPage-main {
    padding: 5px;
    border: 1px solid #b6b6b6; }
    .blogItemPage .blogItemPage-main .blogItemPage_img {
      width: 100%;
      height: 120px; }
    .blogItemPage .blogItemPage-main .blogItemPage-desc h1 {
      padding-top: 20px;
      text-align: center;
      font-size: 18px; }
    .blogItemPage .blogItemPage-main .blogItemPage-desc p {
