        background-size: 20px;
        width: 20px;
        height: 20px;
        margin-right: 15px; }
      .blogItemPage .blogItemPage-main .blogItemPage-tag .blogItem-tag_time {
        float: left;
        background: url("./../images/time.png") no-repeat left center;
        background-size: 18px;
        height: 20px;
        line-height: 20px;
        padding-left: 20px;
        color: #888;
        font-size: 12px; }



