.aboutPage {
  width: 100%;
  margin: 0 auto;
  min-width: 1200px; }
  .aboutPage .aboutPage-banner-fixed {
    position: fixed;
    width: 100%;
    min-width: 1200px;
    background: url("./../images/comment-banner.jpg") no-repeat center center;
    background-size: cover;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -50; }
  .aboutPage .aboutPage-main {
    width: 100%;
    min-width: 1200px;
    margin: 0 auto; }
    .aboutPage .aboutPage-main .aboutPage-main-header {
      width: 100%;
      height: 500px;
      min-width: 1200px;
      background-color: rgba(0, 0, 0, 0.7);
      position: relative; }
      .aboutPage .aboutPage-main .aboutPage-main-header .aboutPage-main-header_content {
        position: absolute;
        width: 500px;
        height: 120px;
        top: 50%;
        left: 50%;
        margin-left: -250px;
        margin-top: -60px;
        text-align: center; }
        .aboutPage .aboutPage-main .aboutPage-main-header .aboutPage-main-header_content .aboutPage-main-header_content_avatar {
          width: 60px;
          height: 60px;
          border-radius: 50%;
          background-color: #fff;
          border: 1px solid #333;
          -webkit-box-shadow: 0 0 2px #333;
          box-shadow: 0 0 2px #333;
          position: relative;
          margin: 0 auto; }
          .aboutPage .aboutPage-main .aboutPage-main-header .aboutPage-main-header_content .aboutPage-main-header_content_avatar img {
            width: 100%;
            height: 100%; }
          .aboutPage .aboutPage-main .aboutPage-main-header .aboutPage-main-header_content .aboutPage-main-header_content_avatar::before {
            position: absolute;
            content: "";
            border-radius: 50%;
            width: 100%;
            height: 100%;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            -webkit-animation: waterWave 4s ease-out 2s infinite;
            animation: waterWave 4s ease-out 2s infinite;
            border: 1px solid #333;
            -webkit-box-shadow: 0 0 2px #333;
            box-shadow: 0 0 2px #333; }
          .aboutPage .aboutPage-main .aboutPage-main-header .aboutPage-main-header_content .aboutPage-main-header_content_avatar::after {
            position: absolute;
            content: "";
            border-radius: 50%;
            width: 100%;
            height: 100%;
            left: 0;
            right: 0;
            top: 0;
            bottom: 0;
            -webkit-animation: waterWave 4s ease-out infinite;
            animation: waterWave 4s ease-out infinite;
            border: 1px solid #333;
            -webkit-box-shadow: 0 0 2px #333;
            box-shadow: 0 0 2px #333; }
        .aboutPage .aboutPage-main .aboutPage-main-header .aboutPage-main-header_content .aboutPage-main-header_content_desc {
          color: #999;
          font-size: 16px; }
    .aboutPage .aboutPage-main .aboutPage-main-content {
      width: 100%;
      min-width: 1200px;
      background-color: rgba(241, 241, 241, 0.6);
      padding: 20px; }
      .aboutPage .aboutPage-main .aboutPage-main-content .aboutPage-main-content-me {
        margin: 0 auto;
        background-color: #fff;
        padding: 20px;
        font-family: Helvetica,STHeiti STXihei,Microsoft JhengHei,Microsoft YaHei,Tohoma,Arial; }
        .aboutPage .aboutPage-main .aboutPage-main-content .aboutPage-main-content-me .aboutPage-main-content-me_title {
          color: #4c4c4c;
          font-size: 20px;
          font-family: Helvetica,STHeiti STXihei,Microsoft JhengHei,Microsoft YaHei,Tohoma,Arial; }
        .aboutPage .aboutPage-main .aboutPage-main-content .aboutPage-main-content-me .aboutPage-main-content-me_desc {
          padding: 20px;
          color: #757575; }
          .aboutPage .aboutPage-main .aboutPage-main-content .aboutPage-main-content-me .aboutPage-main-content-me_desc p {
            margin: 20px 0; }

@-webkit-keyframes waterWave {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: scale(1.6);
    transform: scale(1.6);
    opacity: 0; } }

@keyframes waterWave {
  0% {
    -webkit-transform: scale(1);
    transform: scale(1);
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    -webkit-transform: scale(1.6);
    transform: scale(1.6);
    opacity: 0; } }



