.heroItem {
  width: 380px;
  height: 170px;
  float: left;
  margin: 0 10px 20px;
  background-color: #fff;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s; }
  .heroItem:hover {
    -webkit-box-shadow: 0 0 30px rgba(0, 0, 0, 0.5);
    box-shadow: 0 0 30px rgba(0, 0, 0, 0.5); }
  .heroItem .heroItem-avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background-color: #ccc;
    float: left;
    margin: 15px 0 0 25px; }
  .heroItem .nickname {
    padding: 15px 10px 15px 100px;
    height: 40px;
    line-height: 40px;
    font-size: 16px; }
  .heroItem .heroItem-desc {
    height: 4.5em;
    margin: 0 20px;
    overflow: hidden;
    line-height: 1.5em;
    font-size: 12px;
    color: #888; }
  .heroItem .heroItem-link {
    padding: 10px 20px; }
    .heroItem .heroItem-link .hero-link_blog, .heroItem .heroItem-link .hero-link_git {
      display: inline-block;
      vertical-align: text-top;
      width: 26px;
      height: 26px;
      margin-right: 10px;
      border-radius: 50%;
      text-align: center;
      line-height: 26px;
      color: #fff;
      transition: all .2s ease-out;
      -moz-transition: all .2s ease-out;
      -webkit-transition: all .2s ease-out;
      -o-transition: all .2s ease-out;
      text-decoration: none; }
      .heroItem .heroItem-link .hero-link_blog:hover, .heroItem .heroItem-link .hero-link_git:hover {
        background: transparent;
        -webkit-box-shadow: 0 0 1px #ccc;
        box-shadow: 0 0 1px #ccc;
        color: #333; }
    .heroItem .heroItem-link .hero-link_blog {
      background: green;
      font-size: 14px; }
    .heroItem .heroItem-link .hero-link_git {
      background: #00ae50;
      font-size: 14px; }



