    height: 500px;
    background: url("./../images/b.png") no-repeat top center;
    background-size: cover; }
    .blogPage .blogPage-banner .blogPage-banner-text {
      width: 700px;
      margin-left: 135px;
      padding-top: 70px; }
      .blogPage .blogPage-banner .blogPage-banner-text .blogPage-banner_big {
