.indexPage {
  width: 100%;
  height: 100%;
  background: url("./../images/player.jpg") no-repeat top center;
  background-size: cover;
  position: relative; }
  .indexPage .indexPage_cover {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
