    background-color: rgba(68, 68, 68, 0.3); }
  .indexPage .indexPage_title {
    height: 240px;
    position: absolute;
    left: 0;
    right: 0;
    top: 50%;
    margin: -120px auto 0;
    text-align: center;
    z-index: 1;
    color: #fff;
    padding: 0 100px; }
    .indexPage .indexPage_title .indexPage_title_cn {
      font-size: 20px; }
    .indexPage .indexPage_title .indexPage_jump {
      margin-top: 50px; }
      .indexPage .indexPage_title .indexPage_jump a {
        padding: 6px 12px;
        border: 1px solid #fff;
        border-radius: 4px;
        margin: 0 5px;
        font-size: 12px;
        cursor: pointer;
        -webkit-transition: all 0.2s;
        -o-transition: all 0.2s;
        transition: all 0.2s;
        text-decoration: none;
        color: #fff; }
        .indexPage .indexPage_title .indexPage_jump a:hover {
          background: #fff;
          color: #35abbc; }



