.github {
  width: 360px;
  -webkit-box-shadow: 0 0 2px rgba(0, 0, 0, 0.2);
  box-shadow: 0 0 2px rgba(0, 0, 0, 0.2); }
  .github .github-header {
    position: relative;
    height: 110px;
    background-color: #555; }
    .github .github-header .githuh-avatar {
      position: absolute;
      width: 80px;
      height: 80px;
      border-radius: 50%;
      background-color: #fff;
      padding: 5px;
      bottom: -40px;
      left: 0;
      right: 0;
      margin: 0 auto; }
      .github .github-header .githuh-avatar img {
        width: 100%;
        height: 100%; }
  .github .github-body {
    border-bottom: 1px solid #eee;
    padding-bottom: 20px; }
    .github .github-body .github-title {
      text-align: center;
      font-size: 18px;
      margin-top: 60px; }
    .github .github-body .github-follow {
      display: block;
      width: 100px;
      height: 36px;
      margin: 0 auto;
      border: none;
      color: #fff;
      background: #fa7d3c;
      background: -webkit-linear-gradient(top, #fa7d3c, #f55f10);
      background: -o-linear-gradient(top, #fa7d3c 0, #f55f10 100%);
      background: -webkit-gradient(linear, bottom top, color-stop(0, #fa7d3c), to(#f55f10));
      background: -webkit-linear-gradient(top bottom, #fa7d3c 0, #f55f10);
      background: -o-linear-gradient(top bottom, #fa7d3c 0, #f55f10);
      background: linear-gradient(top bottom, #fa7d3c 0, #f55f10); }
  .github .github-foot {
    overflow: hidden; }
    .github .github-foot a {
      display: block;
      width: 33.333%;
      float: left;
      padding: 10px 0;
      text-decoration: none;
      text-align: center; }
      .github .github-foot a strong {
        font-size: 20px;
        display: block;
        text-align: center;
        line-height: 1.2em; }
      .github .github-foot a span {
        font-size: 12px;
        color: #aaa; }



