.header .header-nav {
  width: 100%;
  height: 60px;
  min-width: 1000px;
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  z-index: 1; }

.header .headerPage {
  height: 60px;
  width: 100%;
  min-width: 1000px;
  position: fixed;
  left: 0;
  top: -60px;
  right: 0;
  z-index: 9;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  transition: all 0.3s;
  background-color: rgba(255, 255, 255, 0.9);
  -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.4); }
  .header .headerPage .headerPage-header {
    width: 1000px;
    height: 60px;
    margin: 0 auto; }
    .header .headerPage .headerPage-header .headerPage-header_list li {
      line-height: 60px;
      float: left;
      color: #fff;
      margin-right: 30px;
      padding: 0 20px;
      position: relative;
      cursor: default; }
      .header .headerPage .headerPage-header .headerPage-header_list li a {
        text-decoration: none;
        color: #333; }
      .header .headerPage .headerPage-header .headerPage-header_list li:nth-of-type(1) {
        background: url("./../images/index.png") no-repeat left center;
        background-size: 16px; }
      .header .headerPage .headerPage-header .headerPage-header_list li:nth-of-type(2) {
        background: url("./../images/blog.png") no-repeat left center;
        background-size: 16px; }
      .header .headerPage .headerPage-header .headerPage-header_list li:nth-of-type(3) {
        background: url("./../images/cc-plane.png") no-repeat left center;
        background-size: 16px; }
      .header .headerPage .headerPage-header .headerPage-header_list li:nth-of-type(4) {
        background: url("./../images/resource.png") no-repeat left center;
        background-size: 16px; }
      .header .headerPage .headerPage-header .headerPage-header_list li:nth-of-type(5) {
        background: url("./../images/comment.png") no-repeat left center;
        background-size: 16px; }
      .header .headerPage .headerPage-header .headerPage-header_list li:nth-of-type(6) {
        background: url("./../images/link.png") no-repeat left center;
        background-size: 16px; }
      .header .headerPage .headerPage-header .headerPage-header_list li span {
        display: block;
        position: absolute;
        width: 0;
        left: 0;
        bottom: 0;
        border-bottom: 3px solid #333;
        -webkit-transition: width 350ms,left 350ms;
        -o-transition: width 350ms,left 350ms;
        transition: width 350ms,left 350ms;
        -webkit-box-sizing: border-box;
        box-sizing: border-box; }
      .header .headerPage .headerPage-header .headerPage-header_list li:hover span {
        position: absolute;
        width: 100%;
        left: 0;
        -webkit-transition: width 350ms;
        -o-transition: width 350ms;
        transition: width 350ms; }
    .header .headerPage .headerPage-header .headerPage-header-avatar {
      float: right;
      width: 40px;
      height: 40px;
      margin-top: 10px;
      border-radius: 50%;
      background-color: #fff;
      border: 1px solid #333;
      -webkit-box-shadow: 0 0 2px #333;
      box-shadow: 0 0 2px #333;
      position: relative;
