        }*/ }
      .header .headerPage .headerPage-header .headerPage-header-avatar img {
        width: 100%;
        height: 100%; }

@-webkit-keyframes waterWave {
  0% {
    -webkit-transform: scale(0.8) rotate(0deg);
    transform: scale(0.8) rotate(0deg);
    opacity: 0; }
  50% {
    opacity: 0.5; }
  100% {
    -webkit-transform: scale(1.6) rotate(360deg);
    transform: scale(1.6) rotate(360deg);
    opacity: 0; } }

@keyframes waterWave {
  0% {
    -webkit-transform: scale(0.8) rotate(0deg);
    transform: scale(0.8) rotate(0deg);
    opacity: 0; }
  50% {
    opacity: 0.5; }
  100% {
    -webkit-transform: scale(1.6) rotate(360deg);
    transform: scale(1.6) rotate(360deg);
    opacity: 0; } }



