.app {
  width: 100%;
  height: 100%; }
  .app .transitionWrapper-enter-active {
    -webkit-animation: shadeIn 1.5s linear forwards;
    animation: shadeIn 1.5s linear forwards; }
  .app .transitionWrapper-leave-active {
    -webkit-animation: shadeOut 1.5s linear forwards;
    animation: shadeOut 1.5s linear forwards; }
  .app .transitionWrapper {
    width: 100%;
    height: 100%;
    position: relative; }

@-webkit-keyframes shadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-10px);
    transform: translateX(-10px); }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0px);
    transform: translateX(0px); } }

@keyframes shadeIn {
  0% {
    opacity: 0;
    -webkit-transform: translateX(-10px);
    transform: translateX(-10px); }
  100% {
    opacity: 1;
    -webkit-transform: translateX(0px);
    transform: translateX(0px); } }

@-webkit-keyframes shadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes shadeOut {
  0% {
    opacity: 1; }
  100% {
    opacity: 0; } }



